(function (global, factory) {
  typeof exports === 'object' && typeof module !== 'undefined' ? factory(require('socket.io-client')) :
  typeof define === 'function' && define.amd ? define(['socket.io-client'], factory) :
  (global = global || self, factory(global.io));
}(this, function (io) { 'use strict';

  io = io && io.hasOwnProperty('default') ? io['default'] : io;

  var connected = false;
  var socket = null;

  var isConnected = function(){
    if (!connected && !socket){
      return false;
    } else {
      return true;
    }
  };

  exports.connect = function(callback){
    let server = {
      default: {
        protocol: 'http',
        ip: 'localhost',
        port: 3000
      },
      development: {
        protocol: 'http',
        ip: 'localhost',
        port: 3000
      },
      production: {
        protocol: 'https',
        ip: 'www.caol.xyz',
        port: null
      }
    };

    let config = server.production;
    let serverUrl = null;

    {
      serverUrl = `${config.protocol}://${config.ip}`;
    }

    try {
      socket = io(serverUrl);
    } catch(err){
      console.error("caol could not connect.");
      // TODO: Invesitgate retries + how socket client handles retries and back-offs.
      callback && callback(connected);
    }

    socket.on('ping', function(data){
      socket.emit('pong', {beat: 1});
    });

    socket.on('disconnected', ()=>{
      connected = false;
      callback && callback(connected);
    });

    connected = true;
    callback && callback(connected);

  };// connect()

  var tempData = {};



  exports.on = function(id, callback) {
    if (!isConnected()){
      throw new Error("caol is not connected. Use caol.connect()");
      return false;
    }
    socket.emit('listenref', id);
    socket.on('recieveref', function (data) {
      /* If the tempData is an [], spread it, if it's not, keep it as it is */
      let newData = null;
      if (Array.isArray(tempData)){
        newData = Object.assign({}, ...tempData, data);
      } else {
        newData = Object.assign({}, tempData, data);
      }
      delete newData['_id'];

      tempData = newData; // sync the local state
      callback(newData);
    });
  };

  exports.set = function(data, id) {
    if (!isConnected()){
      throw new Error("caol is not connected. Use caol.connect()");
      return false;
    }

    tempData = data; // cache the data being set locally.
    // TODO: Better way to do this??
    var newData = Object.assign({}, data, tempData);
    socket.emit('setdata', newData, id);
  };


  exports.get = function(id, callback) {

    if (!isConnected()){
      throw new Error("caol is not connected. Use caol.connect()");
      return false;
    }

    socket.emit('listenref', id);

    // When recievedata is called, then call the users
    // suppllied callback with the data returned
    socket.on('recievedata', function (data){
      tempData = data;
      var combined = Object.assign({}, ...data, ...tempData);
      delete combined['_id'];

      callback(combined);
    });
    // Send out the action of getting all the data
    socket.emit('getall', id);
  };


  exports.disconnect = function() {

    if (!isConnected()){
      throw new Error("caol is not connected. Use caol.connect()");
      return false;
    }

    socket.disconnect();
    socket = null;

  };

}));
